@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400;500;700;900&display=swap);
html,
body {
  background: rgb(0, 92, 184);
  background: linear-gradient(
      135deg,
      rgb(0, 92, 184) 0%,
      rgba(0, 45, 98, 1) 50%,
      rgba(0, 13, 28, 1) 100%
    )
    fixed;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: 0;
  padding: 0;
}

.App {
  color: #fff;
  text-align: center;
}

.Home {
  text-align: left;
}

.UkanaHeader,
.UkanaHeaderJp {
  display: inline-block;
  font-size: 5rem;
  font-weight: 500;
  margin: 0;
  margin-left: 1rem;
}

.UkanaHeaderJp {
  color: #828282;
  font-weight: 300;
}

.Subheader {
  margin: 0;
  margin-left: 1rem;
  font-style: italic;
}

.KanjiCard {
  background: #fff;
  border-radius: 20px;
  color: #000;
  margin: auto;
  padding: 0.5rem;
  text-align: left;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.KanjiCardHeader {
  text-align: center;
}

.KanjiCardKanji {
  font-size: 5rem;
  margin: 0;
}

.KanjiCardMeaning {
  margin: 0;
}

